import { login, register } from '@/api/'
const Cookie = process.client ? require('js-cookie') : void 0
export default {
  middleware: ['notAuthentecated'],
  name: 'loginIndex',
  data () {
    return {
      user: {
        username: '',
        email: '',
        password: ''
      },
      errors: {}
    }
  },
  computed: {
    isLogin () {
      return this.$route.name === 'login'
    }
  },
  methods: {
    async onSubmit () {
      try {
        const { data } = await (this.isLogin ? login : register)({ user: this.user })
        this.$store.commit('SET_USER', data.user)
        Cookie.set('user', data.user)
        this.$router.push('/')
      } catch (error) {
        this.errors = error.response.data.errors
      }
    }
  }
}
